html {
  font-size: 100%;
  font-family: "Rubik", sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
}

ol,
ul {
  list-style: none;
}

img {
  pointer-events: none;
  user-select: none;
}

@media (max-width: 46.1875em) {
  .container--m, .user, .catalog {
    width: 87.46%;
    max-width: 24rem;
    margin: 0 auto;
  }
}

.flex, .user__info, .user__timeframe, .catalog__time {
  display: flex;
  align-items: center;
}
@media (min-width: 46.25rem) {
  .flex, .user__info, .user__timeframe, .catalog__time {
    flex-direction: column;
    align-items: flex-start;
  }
}

.center, .catalog__dots {
  display: flex;
  justify-content: center;
  align-items: center;
}

main {
  background-color: #0D1323;
  min-height: 100vh;
  padding: 5.125rem 0;
}

.user {
  background-color: #1D204B;
  border-radius: 16px;
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.4);
}
.user__info {
  background-color: #5746EA;
  border-radius: 16px;
  gap: 18px;
  padding: 32px 32px 28px 30px;
}
@media (min-width: 46.25rem) {
  .user__info {
    padding: 32px 32px 94px 30px;
    gap: 32px;
  }
}
.user__avatar img {
  width: 4.25rem;
  box-shadow: 0 0 0 2px #fff;
  border-radius: 50%;
}
.user__name small {
  color: #A69DFF;
  font-size: 0.9375rem;
}
.user__name p {
  color: #D3CBFF;
  font-size: 1.46875rem;
}
@media (min-width: 46.25rem) {
  .user__name p {
    font-size: 2.25rem;
    max-width: 6ch;
    margin-top: 6px;
  }
}
.user__timeframe {
  justify-content: space-around;
  height: 4.5rem;
}
@media (min-width: 46.25rem) {
  .user__timeframe {
    padding: 0.75rem 1.875rem;
    height: 10.9375rem;
  }
}
.user__timeframe p {
  font-size: 1.125rem;
  color: #7073B8;
}
.user__timeframe p.user__timeframe--active, .user__timeframe p:hover {
  color: #fff;
  cursor: pointer;
  transition: 0.4s ease;
}

.catalog {
  border-radius: 16px;
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.4);
  position: relative;
}
.catalog.Work {
  background-color: #FF8B64;
}
.catalog.Play {
  background-color: #56C2E6;
}
.catalog.Study {
  background-color: #FF5E7D;
}
.catalog.Exercise {
  background-color: #4BCF83;
}
.catalog.Social {
  background-color: #7235D1;
}
.catalog.SelfCare {
  background-color: #F1C75B;
}
@media (max-width: 46.1875em) {
  .catalog:not(:first-child) {
    margin-top: 1.5rem;
  }
}
.catalog__top {
  height: 2.375rem;
  position: relative;
  overflow: hidden;
}
.catalog__top img {
  position: absolute;
  right: 16px;
  top: -10px;
  width: 5.125rem;
}
.catalog__bottom {
  border-radius: 12px;
  background-color: #1D204B;
  height: 7.625rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 1.5rem;
}
@media (min-width: 46.25rem) {
  .catalog__bottom {
    height: 12.75rem;
  }
}
.catalog__bottom:hover {
  background-color: #34397B;
  transition: 0.4s ease;
  cursor: pointer;
}
.catalog__dots {
  position: absolute;
  right: 0;
  gap: 3px;
  cursor: pointer;
  right: 26px;
  top: 76px;
}
.catalog__dots span {
  border-radius: 50%;
  width: 5px;
  aspect-ratio: 1/1;
  background-color: #B2B5EB;
}
.catalog__title p {
  font-size: 1.125rem;
  font-weight: 400;
  color: #fff;
}
.catalog__time {
  justify-content: space-between;
  margin-top: 0.375rem;
}
@media (min-width: 46.25rem) {
  .catalog__time {
    margin-top: 2.25rem;
    gap: 16px;
  }
}
.catalog__time--current {
  color: #fff;
  font-size: 2rem;
  font-weight: 300;
}
@media (min-width: 46.25rem) {
  .catalog__time--current {
    font-size: 2.625rem;
  }
}
.catalog__time--previous {
  color: #C0C3F0;
  font-size: 16px;
}

@media (min-width: 46.25rem) {
  main {
    display: grid;
    grid-auto-columns: 15.9375rem;
    grid-auto-rows: 15.125rem;
    gap: 32px;
    justify-content: center;
    grid-template-areas: "user card1" "user card2" "card3 card4" "card5 card6";
  }

  .user {
    grid-area: user;
  }

  .catalog.Work {
    grid-area: card1;
  }
  .catalog.Study {
    grid-area: card2;
  }
  .catalog.Play {
    grid-area: card3;
  }
  .catalog.Exercise {
    grid-area: card4;
  }
  .catalog.Social {
    grid-area: card5;
  }
  .catalog.Self.Care {
    grid-area: card6;
  }
}
@media (min-width: 64rem) {
  main {
    display: grid;
    grid-auto-columns: clamp(12.875rem, 12.875rem + (1vw - 0.64rem) * 11.7371, 16rem);
    grid-auto-rows: 15.125rem;
    gap: 32px;
    justify-content: center;
    grid-template-areas: "user card1 card2 card3" "user card4 card5 card6";
  }
}